$container-size: 768px;
$bg: #000;
$text: #fff;
$primary-yellow: #f9f929;

*, *::after, *::before {
	box-sizing: border-box;
 }

 body {
	background: $bg;
	color: $text;
	font-family: 'Lato', sans-serif;
	margin: 0;
	padding: 0;
 }

 .container {
	margin: 0 auto;
	max-width: $container-size;
	text-align: center;

	h1 {
	  display: inline-block;
	  font-size: 36px;
	}

	span {
	  color: $primary-yellow;
	  font-size: 36px;
	  margin-left: 10px;
	}
 }